<template>
    <div>
        <van-nav-bar title=""  class="topbar">
            <!-- <div class=""></div> -->
            <van-icon name="search" slot="right" />
            <van-icon name="arrow-left" slot="left" class="backBtn" to="/main" @click="$router.go(-1)"/>
        </van-nav-bar>
        <!-- item2 -->
        <!-- <h4><span style="color:red"></span>,</h4> -->
        

        <div class="content">
            <div class="left" ref="left">
                <ul  class="clearfix">
                    <li v-for="(item,index) in left"  :class="{current:index==currentIndex}" @click="selectItem(index,$event)" :key="index">
                        <span class="left-item">{{item}}</span>
                    </li>
                </ul>
            </div>
            <div class="right" ref="right">
                <!-- <div class="rolltop">
                    <img src="/img/roll-top.jpg" alt="">
                </div> -->
                <ul class="clearfix">
                    <!--  -->
                    <li v-for="(item,index) in right" class="right-item right-item-hook" :key="index">
                        <div class="roll-title">
                            <span>{{item.name}}</span>
                            </div>
                        <ul class="product-phone clearfix">
                            <li @click="toinfo">
                                <div class="imgbox">
                                    <img src="/img/roll-phone1.png" alt="">
                                </div>
                                <div class="name">
                                    3
                                </div>
                            </li>
                            <li @click="toinfo">
                                <div class="imgbox">
                                    <img src="/img/roll-phone2.png" alt="">
                                </div>
                                <div class="name">
                                    3
                                </div>
                            </li>
                            <li @click="toinfo">
                                <div class="imgbox">
                                    <img src="/img/roll-phone1.png" alt="">
                                </div>
                                <div class="name">
                                    3
                                </div>
                            </li>
                            <li @click="toinfo">
                                <div class="imgbox">
                                    <img src="/img/roll-phone2.png" alt="">
                                </div>
                                <div class="name">
                                    3
                                </div>
                            </li>
                            <li @click="toinfo">
                                <div class="imgbox">
                                    <img src="/img/roll-phone1.png" alt="">
                                </div>
                                <div class="name">
                                    3
                                </div>
                            </li>
                            <li @click="toinfo">
                                <div class="imgbox">
                                    <img src="/img/roll-phone1.png" alt="">
                                </div>
                                <div class="name">
                                    3
                                </div>
                            </li>
                            <li @click="toinfo">
                                <div class="imgbox">
                                    <img src="/img/roll-phone1.png" alt="">
                                </div>
                                <div class="name">
                                    3
                                </div>
                            </li>
                        </ul>
                    </li>
                    
                </ul>
            </div>
        </div>



    </div>
</template>

<script>
    import BScroll from 'better-scroll'

    export default {
        data:function(){
            return {
                left:["","","","Redmi","","",],
                right:[
                    {name:"",content:[1,2,3,4,5,6]},
                    {name:"",content:[1,2,3,4]},
                    {name:"",content:[1,2,3,4,5]},
                    {name:"Redmi",content:[1,2,3]},
                    {name:"",content:[1,2,3,4]},
                    {name:"",content:[1,2,3]}
                ],
                scrollY:0,
                listHeight:[]  //li
            }
        },
        methods: {
            test(){
                this.$router.push({path:"/main"})
            },
            toinfo(){
                this.$router.push({path:"/miinfo"})
            },
            _initScroll(){
                    // 
                    this.lefts = new BScroll(this.$refs.left,{
                        click:true, //
                        probeType:3 //
                    });
                    this.rights = new BScroll(this.$refs.right,{
                        click:true, //
                        probeType:3 //
                    });
                    // console.log(this.rights)
                    // right
                    this.rights.on("scroll",(pos)=>{
                        this.scrollY = Math.abs(Math.round(pos.y));
                    })
        },
        
            _getHeight(){
                let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                let height = 0;//
                this.listHeight.push(height); //
                // 
                for(let i = 0;i < rightItems.length;i++){
                    let item = rightItems[i];
                    height += item.clientHeight; // 
                    this.listHeight.push(height);
                }
            },
            // 
            // index 
            // event 
            selectItem(index,event){
                // better-scrolleventevent_constructed
                // _constructedreturn
                if(!event._constructed){
                    return;
                }else{
                    // 
                    let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                    let el = rightItems[index];
                    // scrollToElement()//
                    this.rights.scrollToElement(el,0);
                
                    //
                    let leftItems = this.$refs.left.getElementsByTagName("li") 
                    let elLi = leftItems[index]
                    this.lefts.scrollToElement(elLi,0);
                
                }
            }
        },

        computed: {
                currentIndex(){
                    for(let i = 0; i < this.listHeight.length;i++){
                        let height = this.listHeight[i];
                        let height2 = this.listHeight[i+1];
                        // height2heightheight2
                        if(!height2 || (this.scrollY >=height && this.scrollY < height2) ){
                            let leftItems = this.$refs.left.getElementsByTagName("li") 
                            let elLi = leftItems[i];
                            this.lefts.scrollToElement(elLi,0);
                            return i;
                        }
                    }
                    // this.listHeight0
                    return 0;
                }
        },
        // 
            mounted() {  //
                // html
                // 
                // $nextTick() DOM
                this.$nextTick(()=>{
                    this._initScroll();//
                    this._getHeight(); //
                    // console.log("111")

                    // 
                    if("id" in this.$route.query){
                        // console.log(this.$route.query.id)
                        // 
                        let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                        let el = rightItems[this.$route.query.id];
                        this.rights.scrollToElement(el);
                    }
                })

                


            }
}
</script>

<style lang="scss" scoped>
        .van-nav-bar .van-icon{
            font-size: 20px;
        }           
