            padding:0px;
            margin:0px;
        }
        ul{
            list-style: none;
        }
        .content{
            display: flex;
            position: absolute;
            /*  */
            top:50px;
            bottom: 50px;
            // background-color: #eeeeee;
            width: 100%;
            overflow: hidden;
        }

        .rolltop{
            width: 80%;
            height:115px;
            margin: 0 auto;
            img{
                width: 100%;
                height: 100%;
            }
        }
        .left{
            flex:0 0 80px;
            width: 80px;
            background-color: #fff;
        }
        .left li{
            width:100%;
            height: 100%;
        }
        .left li.current{
            // background-color: #007AAE;
            color:#fb7d34;
            font-weight: bold;
        }
        .left-item{
            display: block;
            width: 100%;
            height: 100%;
            line-height: 50px;
            height: 50px;
            text-align: center;
        }
        .right{
            flex:1;
            // background-color: azure;
        }
        .right li{
            width:100%;
        }
        // .right-item li{
        //     width: 100%;
        //     height: 100px;
        //     line-height: 100px;
        //     text-align: center;
        //     border: 1px solid yellow;
        // }
        .clearfix::after{
            content: "";
            clear: both;
            display: block;
        }

        .van-nav-bar{
            background-color: rgb(242, 242, 242);
            .van-nav-bar__title{
                max-width: 100%;
            }
            .van-icon{
                color: black;
            }
        }

        .roll-title{
            text-align: center;
            height: 70px;
            line-height: 70px;
            font-size: 16px;
            span{
                position: relative;
            }
        }
        
        .roll-title span::after{
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 20px;
            border-top: 1px solid #e0e0e0;
            left: auto;
            right: 0;
            transform: translate3d(150%,-50%,0);
    
        }
        .roll-title span::before{
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 20px;
            border-top: 1px solid #e0e0e0;
            transform: translate3d(-150%,-50%,0);
            // -webkit-transform: translate3d(-150%,-50%,0);
    
        }

        .product-phone{
            width: 100%;

            li{
                float: left;                
                width: 33.333333333333336%;
                text-align: center;
                margin-top: .2rem;
                margin-bottom: .3rem;
                overflow: hidden;
                .imgbox{
                    width: 76px;
                    height: 76px;
                    margin: 0 auto;
                    background: #fff;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }

                .name{
                    margin-top: .28rem;
                    white-space: nowrap;
                    font-size: .23rem;
                    color: rgba(0,0,0,.54);
                }
            }
        }

</style>